.node {
    background-color: white;
    border-radius: 2px;
    border-style: solid;
    border-color: rgb(0, 0, 0);
    border-width: 1px;
   
    width: 1.5rem;
    height: 1.5rem; 
  }

.open{
  background-color: #702f04;
}

.visited{
  background-color: #c5853d;
} 

.path{
  animation-name: animatePath;
  animation-duration: 0.5s;
  animation-timing-function: linear;
  animation-delay: 0;
  animation-fill-mode: forwards;
}
  
@keyframes animatePath {
  0% {
    transform: scale(0.6);
    background-color: rgb(4, 58, 112);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(32, 107, 182);
  }

  100% {
    transform: scale(1);
    background-color: rgb(130, 192, 255);
  }
}

.wall{
  background-color: black;
}
.start{
  background-color: green;
}

